#root {
    margin: 0 auto;
}

.canvas-wrapper {
    position: sticky;
    width: 100%;
    height: calc(100vh - 30px);
    top: 15px;
}

canvas {
    position: absolute;
    width: 100%;
    height: 100%;
    transition: opacity 200ms ease-in;
}

.canvas_hidden {
    opacity: 0;
}

@keyframes spinner {
    to {transform: rotate(360deg);}
  }

.rotate {
    width: 16px;
    height: 16px;
    transition: opacity 200ms ease-in;
    animation: spinner .6s linear infinite;
}

.spinner_hidden {
    opacity: 0;
}